<!-- 兑奖中 -->
<template>
	<view class="my-card-item" @click="lookExchangeCode">
		<!-- 头部 -->
		<view class="my-card-item-header">
			<text class="shop-name">{{config.shop_name}}</text>
			<text class="sign">{{title}}</text>
		</view>
		<!-- 主体 -->
		<view class="my-card-item-body">
			<!-- 中间信息 -->
			<view class="ieb-info">
				<view class="ieb-info-text effective-time">
					有效期剩余：{{config.remainingTime|remainingTime}}
				</view>
				<view class="ieb-info-text pot-num">
					罐数：{{config.card_count}}罐
				</view>
				<view class="ieb-info-text paid">
					已支付：¥{{config.pay_money}}
				</view>
			</view>
			<!-- 查看卷码 -->
			<view v-if="config.status === 0" class="look" hover-class="look-hover">
				查看兑换码
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			config: {
				type: Object
			},
			title:{
				type:String,
				default:'兑奖中'
			}
		},
		filters: {
			remainingTime(val) {

				let dir = val <= 0 ? '-' : ''
				val = Math.abs(val) / 1000
				let h = parseInt(val / (60 * 60)) % 24
				let m = parseInt(val / (60)) % 60
				let s = parseInt(val % 60)
				m = m > 9 ? m : '0' + m
				s = s > 9 ? s : '0' + s
				h = h > 9 ? h : '0' + h
				return dir + h + ':' + m + ':' + s
			}
		},
		methods: {
			lookExchangeCode() {
				uni.navigateTo({
					url: `/pages/personal/exchangeCode/index?codeData=${this.config.out_trade_no}`
				})
			}
		}
		// ,beforeDestroy(){

		// }
	}
</script>

